import React, { useState, useEffect, useContext } from 'react'
import { Link, useParams } from 'react-router-dom'
import ReactPlayer from 'react-player'
import context from '../../../Context/Context'
import VidBox from '../Common/VidBox'

const VideoMain = () => {

  const { videos,PlayerName,mainUrl } = useContext(context);
  const [videodetail, setVideodetail] = useState(null);

  console.log(videodetail);

  const { id } = useParams();

  useEffect(() => {
    mainUrl(`videos?part=snippet,statistics&id=${id}`)
      .then((data) => setVideodetail(data.items[0]));
  }, [id]);

  let url = `https://www.youtube.com/watch?v=${id}`
  let dummyUrl = 'https://www.youtube.com/watch?v=qZVTkn2NjS0';

  return (
    <div className='videoPlayerMain'>
      <div className='container'>
        <div className='Video-list-wrapper row'>
          <div className='col-md-8 col-xl-8 col-12 col-sm-12'>
            <div className='vidHolder'>
              <ReactPlayer
                className="react-player"
                url={url ? url : dummyUrl}
                controls />
              <div className='vid-title'>
              {PlayerName}
              </div>
            </div>

          </div>
          <div className='col-md-4 col-xl-4 col-12 col-sm-12'>
            <div className='ListHolder'>
              <VidBox videos={videos} />
            </div>
          </div>
        </div>

      </div>
    </div>
  )
}

export default VideoMain