import { BrowserRouter, Routes, Route } from 'react-router-dom';
import ChannelDetail from './Assets/Components/Channel/ChannelDetail';
import ErrorPage from './Assets/Components/Common/ErrorPage';
import HomeComponent from './Assets/Components/HomeComponent';
import Navbar from './Assets/Components/Navbar';
import VideoMain from './Assets/Components/Video/VideoMain';
import './Assets/Styles/styles.css'

function App() {
  return (
    <BrowserRouter>
      <div className="main_wrapper">
        <Navbar />
        <Routes >
          <Route path='/' exact element={<HomeComponent />} />
          <Route path='/youtube' exact element={<HomeComponent />} />
          <Route path='/video/:id' exact element={<VideoMain />} />
          <Route path='/channel/:id' element={<ChannelDetail />} />
          <Route path='*' element={<ErrorPage />} />
        </Routes>
      </div>
    </BrowserRouter>
  );
}

export default App;
