import React,{useContext} from 'react';
import LogoMain from '../Images/Logo_Main.png';
import Context from '../../Context/Context';
import { Link } from 'react-router-dom';

const Navbar = () => {

    const {SearchInput,setSearchInput , SearchFun , scroll } = useContext(Context);
    return (
        <>
            <nav className={scroll? "navbar topNav navbar-expand-lg scroll": 'navbar topNav navbar-expand-lg'}>
                <div className="container">
                    <Link to='/' className="navbar-brand" >
                        <div className='img-sec'>
                            <img src={LogoMain} alt="brandName" />
                        </div>
                        <div className='text'>Tube</div>
                    </Link>
                    <button
                        className="navbar-toggler"
                        type="button"
                        data-bs-toggle="collapse"
                        data-bs-target="#navbarSupportedContent"
                        aria-controls="navbarSupportedContent"
                        aria-expanded="false"
                        aria-label="Toggle navigation"
                    >
                        <span className="fas fa-bars text-light" />
                    </button>
                    <div className="collapse navbar-collapse" id="navbarSupportedContent">
                        <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                            <li className="nav-item">
                                <Link to='/' className="nav-link active" aria-current="page" >
                                    <i className='fas fa-home ' />
                                </Link>
                            </li>
                            <li className="nav-item dropdown">
                                <span
                                    className="nav-link dropdown-toggle"
                                    id="navbarDropdown"
                                    role="button"
                                    data-bs-toggle="dropdown"
                                    aria-expanded="false"
                                >
                                    <i className='fas fa-user' />   
                                </span>
                                <ul className="dropdown-menu" aria-labelledby="navbarDropdown">
                                    <li>
                                        <span className="dropdown-item" >
                                            Login
                                        </span>
                                    </li>
                                    <li>
                                        <span className="dropdown-item" >
                                            Setttings
                                        </span>
                                    </li>
                                </ul>
                            </li>
                        </ul>
                        <form className="SearchBar" onSubmit={SearchFun}>
                            <input
                                onChange={(e) => {setSearchInput(e.target.value)}}
                                value={SearchInput}
                                className="form-control me-2"
                                type="search"
                                placeholder="Search"
                                aria-label="Search"
                            />
                            <button className='searcicon fas fa-search' type="submit"></button>
                        </form>
                    </div>
                </div>
            </nav>

        </>
    )
}

export default Navbar