import React,{useContext} from 'react'
import { Link } from 'react-router-dom';
import context from '../../../Context/Context';
const VidSub = ({ video: { id: { videoId }, snippet } }) => {

    const {setPlayerName} = useContext(context);

    let demoVideoUrl = 'https://www.youtube.com/watch?v=FHTbsZEJspU&list=PL6QREj8te1P6wX9m5KnicnDVEucbOPsqR&index=1&t=511s';
    let demoTitle = "Ajay Choudhary";
    let lolVid = 'https://i.ytimg.com/vi/tLN798f8g8k/hqdefault.jpg';
    let lolVid1 = 'https://i.ytimg.com/vi/AZbMYWP8l3E/hqdefault_live.jpg';
    let DefaultVideo = 'https://pbs.twimg.com/media/EJfbmvqWsAIUKgV.jpg';
    let mainUrl = snippet.thumbnails.high.url;
    
    return (
        <>
            <Link to={videoId ? `/video/${videoId}` : demoVideoUrl} onClick={() =>{setPlayerName(snippet?.title)}}>
                <div className='vid-main-card'>
                    <div className='video-top-box'></div>
                    <div className='video-box'> <img src={ mainUrl === lolVid && lolVid1 ? DefaultVideo  : mainUrl} alt={snippet?.title} /> </div>
                    <div className='detail-sec'>
                        <div className='image-sec'> </div>
                        <div className='vidName'>{snippet?.title.slice(0, 25)}...</div>

                        <Link to={snippet?.channelId ? `/channel/${snippet?.channelId}` : demoTitle} className='channelName'>
                            {snippet?.channelTitle}
                        </Link>

                        <div className='more-icon'>
                            <div className=''> </div>
                        </div>
                    </div>
                </div>
            </Link>
        </>
    )
}

export default VidSub

