import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import { api } from '../../../Utilities/Api';
import VidBox from '../Common/VidBox';
import ChannelCard from './ChannelCard';

const ChannelDetail = () => {

    const [channelDetail, setchannelDetail] = useState('');
    const [videos, setvideos] = useState([])

    const { id } = useParams();

    console.log(channelDetail, videos);

    let dummyCover = 'https://i1.wp.com/cdn.techgyd.com/35-Most-Amazing-Facebook-Cover-Photos-HD-16.jpg?resize=722%2C266';

    useEffect(() => {
        api(`channels?part=snippet&id=${id}`)
            .then((data) => setchannelDetail(data?.items[0]));
        api(`search?channelId=${id}&part=snippet&order=date`)
            .then((data) => setvideos(data?.items));
    }, [id])

    return (
        <>
            <div className='chnl-Dtl-Wrpper'>
                <div className='TopHeader'>
                    <div className='ChannelHeader'>
                        <img className='CoverImg' src={dummyCover} alt={dummyCover} />
                    </div>
                    <div className='col-md-3 col-xl-3 col-sm-6 col-6 m-auto '>
                        <ChannelCard channelDetail={channelDetail} />
                    </div>
                </div>
                <div className='container' >
                    <div className='VidDetail_box'>
                        <VidBox videos={videos} />
                    </div>
                </div>
            </div>
        </>
    )
}

export default ChannelDetail