import React from 'react'
import VidSub from './VidSub';
import ChannelCard from '../Channel/ChannelCard';

const VidBox = ({ videos }) => {

    console.log(videos);
    
    return (
        <>
            <div className='row' >
                {videos.map((item, index) => (
                    <div className='col-md-4 col-xl-3 col-sm-12 col-12 card-holder' key={index}>
                        {item.id.videoId && <VidSub video={item} />}
                        {item.id.channelId && <ChannelCard channelDetail={item} />}
                    </div>
                ))}
            </div>
        </>
    )
}

export default VidBox