import React from 'react'
import { Link } from 'react-router-dom'

const ChannelCard = ({ channelDetail }) => {

  let demoTitle = "ajay Choudhary"
  let demoIMG = 'https://dl.memuplay.com/new_market/img/com.vicman.newprofilepic.icon.2022-06-07-21-33-07.png'

  return (
    <>
      <Link to={`/channel/${channelDetail?.id?.channelId}`}>
        <div className='vid-main-card'>
          <div className='video-top-box'></div>
          <div className='User-bg-img'>
            <img src={channelDetail?.snippet?.thumbnails?.high?.url || demoIMG} alt={channelDetail?.snippet?.title} />
          </div>
          <div className='detail-sec'>
            <div className='image-sec'> </div>
            <Link to={channelDetail?.snippet?.channelId ? `/channel/${channelDetail?.snippet?.channelId}` : demoTitle}
              className='channelName channel'>
              {channelDetail?.snippet?.channelTitle}
            </Link>
            {channelDetail?.statistics?.subscriberCount &&
              <p className='SubsCriberCount'>Subscribed By <span className='SubsNum'>
                {parseInt(channelDetail?.statistics?.subscriberCount).toLocaleString()}
              </span> users
              </p>
            }

            <div className='more-icon'>
              <div className=''> </div>
            </div>
          </div>
        </div>
      </Link>
    </>
  )
}

export default ChannelCard